<template>
  <view class="out-box">
    <cardHeader v-if="headerShow" :imageUrl="headerImageUrl" />
    <view :class="containerClass">
      <view
        v-for="(module, index) in modules"
        :key="index"
        :class="['common-style', module.class]"
        :style="{ backgroundImage: `url(${module.backgroundImage})` }"
        @click="jumpToPath(module)"
      >
        <view class="title" :style="getStyle(module, 'titleColor')">{{ module.title }}</view>
        <view class="tip" :style="getStyle(module, 'tipColor')">{{ module.tip }}</view>
      </view>
    </view>
  </view>
</template>

<script>
import uniStorage from '@/utils/storages/uniStorage'
import cardHeader from './cardHeader.vue'
export default {
  name: 'CardBox',
  components: {
    cardHeader,
  },
  props: {
    dataConfig: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {}
  },
  computed: {
    modules() {
      return this.dataConfig.cardBoxList || []
    },
    containerClass() {
      const length = this.modules.length
      if (length === 1) return 'single-column'
      if (length === 2) return 'two-columns'
      if (length === 3) return 'three-modules-layout'
      return 'card-box-container'
    },
    headerShow() {
      return this.dataConfig.headerShow.value == 1
    },
    headerImageUrl() {
      return this.dataConfig.headerLogoConfig.url
    },
  },
  methods: {
    getStyle(item, type) {
      if (type == 'backgroundImage') {
        return {
          backgroundImage: `url(${item.backgroundImage})`,
        }
      }
      if (type == 'titleColor') {
        return {
          color: item.titleColor,
        }
      }
      if (type == 'tipColor') {
        return {
          color: item.tipColor,
        }
      }
    },
    // 临时方法
    jumpToPath(item) {
      if (item.value === '/pages-sub/realname/index') {
        this.handleAuth()
        return
      }
      this.$jumpToPath(item.value)
    },
    handleToInstructor() {
      uni.switchTab({
        url: '/pages/instructor/index',
      })
    },
    handleToNotice() {
      uni.navigateTo({
        url: '/pages-sub/notice/index',
      })
    },
    async handleAuth() {
      if (!uniStorage.get('is_login')) {
        try {
          await this.$dialog('您还未登录,去登录?', {
            showCancelButton: true,
            confirmButtonText: '确定',
            cancelButtonText: '取消',
          })
        } catch (error) {
          console.log(error)
          return
        }
        uni.navigateTo({
          url: '/pages/login/index',
        })
        return
      }
      if (this.$store.user.userInfo.is_auth_instructor == 1) {
        uni.navigateTo({
          url: '/pages-sub/realname/detail',
        })
      } else {
        uni.navigateTo({
          url: '/pages-sub/realname/index',
        })
      }
    },
  },
}
</script>

